@import "../../styles/variables";
.login {
    display: flex;
    flex-direction: column;
}

.header {
    display: flex;
    justify-content: space-between;
    margin: 15px;
    align-items: center;
    height: 15%;
    .right-side {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        .logo {
            color: white;
            font-weight: 700;
            font-size: 45px;
        }
        .greeding {
            color: white;
        }
    }
}

.credentials {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 70%;
    .input-group {
        width: 70%;
        display: flex;
        justify-content: center;
        margin: 10px;
        label {
            text-align: right;
            color: white;
            font-size: large;
            font-weight: 500;
        }
        input,
        .login-btn {
            color: $main-color;
            font-size: large;
            padding: 15px;
            font-weight: 500;
            text-align: right;
            border-radius: 10px;
            outline: none;
            border: 3px solid $secondary-color;
            &:focus {
                outline: none !important;
                box-shadow: 0 0 10px $secondary-light-color;
            }
        }
        &.username,
        &.password {
            flex-direction: column;
        }
        &.login-btn-wrapper {
            .login-btn {
                width: 100%;
                background-color: #6bb5ff;
                color: white;
                text-align: center;
                border-bottom: 5px solid #a4d1ff;
            }
        }
        &.error {
            color: #ba3939;
            direction: rtl;
            padding: 5px;
            background: #ffe0e0;
            border: 1px solid #a33a3a;
            width: calc(70% - 10px);
            position: relative;
            .close {
                border: 1px solid #ffe0e0;
                color: #ffe0e0;
                background: #a33a3a;
                border-radius: 50%;
                width: 17px;
                height: 17px;
                display: flex;
                justify-content: center;
                align-items: center;
                padding-bottom: 4px;
                position: absolute;
                top: 0;
                margin: -8px;
                right: 0;
                box-sizing: border-box;
            }
        }
    }
}